@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";
@import "src/common/components/button/Button.module.scss";

.contactsBlock {
  @include container();
  @include flex(column, flex-start, center, nowrap);
  background-color: $bgColor;
  min-height: 60vh;
  .formFields {
    @include flex(column, flex-start, stretch, nowrap);
    margin: 30px;
    width: 70%;
    input, textarea {
      width: 100%;
      margin: 5px;
      height: auto;
      background-color: $bgSecondColor;
      border: 1px solid #4a4a4a;
      padding: 5px 15px;
      border-radius: 0;
      line-height: 1.5;
      font: 400 14px/1.8 'Poppins', sans-serif;
      color: $textColor;
